  font-family: sans-serif;
  background-color: #242424; }

.search-box {
  width: 80%;
  height: 3rem;
  font-size: 2rem;
  font-weight: 300;
  color: #929292;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #929292;
