.card {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 280px;
  max-width: 310px;
  width: 100%;
  color: #333;
  text-align: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  font-size: 16px; }
  .card * {
    box-sizing: border-box;
    -webkit-transition: all 0.35s ease;
    transition: all 0.35s ease; }
  .card .image {
    position: relative;
    border-bottom: 4px solid #34495e;
    z-index: 1;
    height: 20rem;
    margin: 0; }
    .card .image:before {
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 20px 20px 0 20px;
      border-color: #34495e transparent transparent transparent;
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      -webkit-transform: translate(-50%, -15px);
              transform: translate(-50%, -15px);
      z-index: -1;
      -webkit-transition: all 0.35s ease;
      transition: all 0.35s ease; }
    .card .image img {
      max-width: 100%;
      max-height: 100%;
      height: 100%;
      vertical-align: top;
      background-color: #929292; }
  .card .caption {
    height: 14rem;
    background-color: #ffffff;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    .card .caption .info-container .office, .card .caption .info-container .party, .card .caption .info-container .email, .card .caption .info-container .phone, .card .caption .info-container .address {
      padding: 1px; }
    .card .caption .info-container .email, .card .caption .info-container .phone, .card .caption .info-container .address {
      font-size: 0.9rem;
      color: #5b5b5b; }
    .card .caption .info-container .address {
      padding-top: 2px; }
    .card .caption .info-container .name {
      font-size: 1.4rem;
      color: #242424;
      padding: 5px; }
      @media screen and (max-width: 767px) {
        .card .caption .info-container .name {
          padding: 3px; } }

@media screen and (min-width: 1200px) {
  .slide-up {
    animation-name: slide-up;
    -webkit-animation-name: slide-up;
    animation-duration: 0.75s;
    -webkit-animation-duration: 0.75s; }
  @keyframes slide-up {
    0% {
      -webkit-transform: translateY(100%);
              transform: translateY(100%); }
    100% {
      -webkit-transform: translateY(0%);
              transform: translateY(0%); } }
  @-webkit-keyframes slide-up {
    0% {
      -webkit-transform: translateY(100%); }
    100% {
      -webkit-transform: translateY(0%); } } }

